:root {
  --g1: #107657;
  --g2: #1F9873;
  --g3: #37AB88;
  --g4: #58C7A5;
  --g5: #85E1C5;

  --white: #fdfdfd;
  --white2: #e9e9e9;

  --black: #212121;

  --big-responsive-font: calc(72px + (56 - 24) * (100vw - 360px)/(960-360));
  --medium-responsive-font: calc(48px + (48 - 20) * (100vw - 360px)/(960-360));
  --small-responsive-font: calc(32px + (32 - 16) * (100vw - 360px)/(960-360));
  --tiny-responsive-font: calc(24px + (24 - 12) * (100vw - 360px)/(960-360));
  --teeny-responsive-font: calc(16px + (20 - 12) * (100vw - 360px)/(960-360));
  --space: calc(3vw + (-1 * (3px + (5 - 1) * (100vw - 360px)/(960-360))));
  --space2: calc(-1 * (32px + (32 - 16) * (100vw - 360px)/(960-360)));
  --space3: calc(3vw + (1px + (3 - 1) * (100vw - 360px)/(960-360)));
}

html, body {
  margin: 0 auto;
  background: var(--g1);
}

nav {
  height: 72px;
  margin: 0 auto;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  position: absolute;
  top: 0;
  right: 0;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  z-index: 10;
}

nav img {
  margin-top: 16px;
}

nav.invert {
  background-color: var(--white);
}

nav img:nth-child(1) {
  height: 24px;
  width: auto;
  margin-left: 24px;
}

nav img:nth-child(2) {
  height: 24px;
  width: auto;
  margin-right: 24px;
}

div.nav-links {
  position: relative;
  background-color: var(--white);
  margin: 0 auto;
  width: 100%;
  margin-top: 71px;
  z-index: 5;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: start;
      align-items: flex-start;
  -webkit-box-shadow: 0px 2px 16px 0px rgba(33,33,33,1);
  box-shadow: 0px 2px 16px 0px rgba(33,33,33,1);
}

div.nav-links-links img {
  height: 40px;
  width: auto;
  margin-left: 12px;
}

ul.inner-content {
  margin-left: 24px;
  margin-top: 24px;
  color: var(--black);
  font-family: 'Lato', sans-serif;
}

ul.inner-content li {
  font-size: 32px;
  margin-bottom: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

ul.inner-content li.hide {
  display: none;
}

ul.inner-content li > img {
  -webkit-transition: -webkit-transform .16s ease;
  transition: -webkit-transform .16s ease;
  -o-transition: transform .16s ease;
  transition: transform .16s ease;
  transition: transform .16s ease, -webkit-transform .16s ease;
  height: 14px;
  display: inline;
  margin-left: 8px;
  margin-top: 4px;
}

ul.inner-content li > img:hover {
  cursor: pointer;
}

img.flip {
  -webkit-transition: -webkit-transform .16s ease;
  transition: -webkit-transform .16s ease;
  -o-transition: transform .16s ease;
  transition: transform .16s ease;
  transition: transform .16s ease, -webkit-transform .16s ease;
  -webkit-transform: rotateX(180deg);
          transform: rotateX(180deg);
}

div.nav-links-links {
  position: relative;
  top: 24px;
  right: 22px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-item-align: end;
      align-self: flex-end;
  margin-bottom: 32px;
  width: 20%;
}

div.nav-links.hide {
  display: none !important;
}

div.nav-links-links img {
  margin-bottom: 16px;
}

/*nav a:nth-child(4) img {
  margin-right: 16px;
}*/

nav img:hover {
  cursor: pointer;
}

header {
  height: calc(100vh - 72px);
  position: absolute;
  top: 72px;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: start;
      align-items: flex-start;
  /*z-index: -1;*/
}

header h1 {
  font-family: 'Lato', sans-serif;
  font-size: 64px;
  font-weight: 700;
  text-align: left;
  color: var(--white);
  margin: 0 auto;
  margin-left: 22px;
  /*margin-top: 64px;*/
}

header p {
  font-family: 'Lato', sans-serif;
  font-size: 24px;
  font-weight: 300;
  color: var(--g5);
  margin-left: 23px;
  margin-right: 24px;
  margin-bottom: 32px;
}

header button {
  margin-left: 24px;
  background-color: var(--g2);
  border-radius: 2px;
  border: none;
  padding: 16px 24px;
  font-size: var(--teeny-responsive-font);
  line-height: var(--tiny-responsive-font);
  color: var(--white);
}

header button:hover {
  background-color: var(--g5);
  -webkit-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease;
  cursor: pointer;
}

header button,
header button:hover,
header button:active {
  outline: none;
}

main {
  margin-top: 100vh;
}

main section {
  background-color: #fdfdfd;
  padding-top: 10vh;
  padding-bottom: 10vh;
  color: #212121;
}

section {
  font-family: 'Lato', sans-serif;
}

section h2 {
  font-size: var(--small-responsive-font);
  margin-bottom: 16px;
  margin-left: 24px;
  margin-right: 24px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: start;
      align-items: flex-start;
}

section > p {
  font-size: var(--tiny-responsive-font);
  font-family: 'Lato', serif;
  margin-left: 24px;
  margin-right: 24px;
  font-weight: 400;
  line-height: 1.6em;
}

section:nth-child(1) img {
  width: 240px;
  height: auto;
  border-radius: 50%;
  margin-top: 16px;
  margin-left: calc(50vw - 128px);
  margin-bottom: 32px;
  border: 8px solid var(--g3);
}

main section:nth-child(2) {
  background-color: var(--black);
  padding-top: 10vh;
  padding-bottom: 10vh;
  color: var(--white);
}

section:nth-child(2) h2 {
  margin-bottom: 48px;
  color: var(--white);
}

div.project {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  width: 94%;
  margin: 48px 24px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

div.project-info {
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: start;
      align-items: flex-start;
}

div.project-info h3 {
  font-size: var(--small-responsive-font);
  color: var(--white);
  margin-bottom: 8px;
}

div.project-info p {
  font-family: 'Lato', serif;
  font-size: var(--teeny-responsive-font);
  line-height: 1.6;
  color: var(--white);
  margin-bottom: 12px;
}

div.project-info p a {
  opacity: 1;
}

div.project-info p:nth-child(2) {
  margin-bottom: 24px;
  opacity: .7;
}

div.project:nth-child(3) {
  margin-bottom: 0;
}

main section div.project > img {
  width: 45%;
  max-width: 480px;
  margin-top: 16px;
  margin-bottom: 16px;
  height: auto;
}

a,
a:hover,
a:active,
a:visited {
  text-decoration: none;
  color: var(--g3);
  font-size: var(--teeny-responsive-font);
  -webkit-transition: color .2s ease;
  -o-transition: color .2s ease;
  transition: color .2s ease;
}

a:hover {
  color: var(--g4);
}

@media screen and (max-width: 640px) {
  div.project {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    max-width: 480px;
    width: calc(100vw - 48px);
    margin: 48px 24px;
  }

  main section div.project > img {
    width: 100%;
    -ms-flex-item-align: center;
        align-self: center;
    max-width: 480px;
    margin-top: 16px;
    margin-bottom: 16px;
    height: auto;
  }

  div.project-info {
    width: 100%;
  }
}



